export default ([{
    path: '/itService',
    meta: {
        title: 'IT服务',
        type: "safem"
    },
    component: resolve => require(['@/views/itService'], resolve),
    children: [

        {
            path: '/',
            redirect: 'informationResourceApply'
        },
        {
            path: 'templatePage',
            meta: {
                title: '空白页面'
            },
            component: resolve => require(['@/components/templatePage'], resolve)
        },
        {
            path: 'informationResourceApply',
            meta: {
                title: '信息资源申请',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/informationResourceApply`], resolve)
        },
        {
            path: 'usersApply',
            meta: {
                title: '用户账号/权限申请',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/usersApply`], resolve)
        },
        {
            path: 'softwareApply',
            meta: {
                title: '办公室软件申请',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/softwareApply`], resolve)
        },
        {
            path: 'informationSystemsApply',
            meta: {
                title: '信息系统需求申请',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/informationSystemsApply`], resolve)
        },
        {
            path: 'serviceRecord',
            meta: {
                title: '服务记录',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/serviceRecord`], resolve)
        },
        {
            path: 'repairApply',
            meta: {
                title: '保障保修申请',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/repairApply`], resolve)
        },
        {
            path: 'dataAccessApply',
            meta: {
                title: '数据接入申请',
                type: "safem"
            },
            component: resolve => require([`@/views/itService/dataAccessApply`], resolve)
        },
        // {
        //     path: 'indexLab',
        //     meta: {
        //         title: '年度目标指标',
        //         type: "safem"
        //     },
        //     component: resolve => require([`@/view/systemManagement/indexLab`], resolve)
        // },
    ]
}])