import request from '@/request/api'
/**===========基本信息接口 */
//获取我的资产的列表
export function getListApi(data = {}) {
    return request({
        url: '/asset/myAssetsAction/queryList',
        method: 'post',
        data: data
    })
}
//我的资产-资产-确认、接受、驳回
export function confirmApi(data = {}) {
    return request({
        url: '/asset/myAssetsAction/confirm',
        method: 'post',
        data: data
    })
}
//我的资产-根据人员查资产
export function getassetsListApi(data = {}) {
    return request({
        url: '/asset/myAssetsAction/queryListByEmpCode.htm',
        method: 'post',
        data: data
    })
}

