/**
 * 搜索栏下拉选项循环
 * @method dealOption
 * @param {Array} obj 从后台拿到的数据,type:1取值，其他取Id
 * @return {Array} arr
 */
utils.dealOption = function (obj, type) {
  var arr = new Array();
  if (obj) {
    obj.forEach(function (item, index) {
      arr.push({ label: item.itemName, itemValue: item.itemCode, value: item.itemCode, name: item.itemName })
    })
  }
  return arr;
}

