/**
 * 将时间戳换成可看的格式
 * */
utils.TimeFormat = function (obj, type) {
  if (obj) {
    if (!utils.isNumber(obj)) {
      return obj
    }
    var time = new Date(obj);
    var y = time.getFullYear();
    var M = time.getMonth() + 1;
    M < 10 ? M = '0' + M : null;
    var d = time.getDate();
    d < 10 ? d = '0' + d : null;
    var h = time.getHours();
    h < 10 ? h = '0' + h : null;
    var m = time.getMinutes();
    m < 10 ? m = '0' + m : null;
    var s = time.getSeconds();
    s < 10 ? s = '0' + s : null;
    if (type === 1) {
      return y + '-' + M + '-' + d;
    }

    return y + '-' + M + '-' + d + " " + h + ':' + m + ':' + s;
  } else {
    return ''
  }
}

utils.getYear = function () {
  return new Date().getFullYear().toString()
}
utils.getYearMonth = function (num = 1) {
  let nowDate = new Date()
  let year = nowDate.getFullYear()
  let month = nowDate.getMonth() + num

  return `${year.toString()}-${month < 10 ? '0' + month : month + ''}`
}

utils.getNowDay = function () {
  let nowDate = new Date()
  let year = nowDate.getFullYear()
  let month = nowDate.getMonth() + 1
  let day = nowDate.getDate()

  return `${year.toString()}-${month < 10 ? '0' + month : month + ''}-${day < 10 ? '0' + day : day}`
}

utils.getWeekByDate = function (str) {

  let date = new Date(str);

  let month = date.getMonth() + 1;

  let year = date.getFullYear();

  let dateN = new Date(str);

  //月份第一天

  dateN = new Date(dateN.setDate(1));

  let w1 = dateN.getDay();

  // 将字符串转为标准时间格式

  let w = date.getDay();//周几

  if (w === 0 && w1 != 0) {//当月第一天不是周天，当前日期是周天

    w = 7;

  }

  let week = Math.ceil((date.getDate() + 6 - w) / 7);

  if (w1 != 1)//当月第一天不是周一

    week = Math.ceil((date.getDate() + 6 - w) / 7) - 1;

  var cNum = ["零", "一", "二", "三", "四", "五", "六", "七", "八", "九"];

  if (week === 0) {//第0周归于上月的最后一周

    month = date.getMonth();

    if (month === 0) {//跨年

      month = 12;

      year = year - 1;

    }

    let dateLast = new Date(date);

    let dayLast = new Date(year, month, 0).getDate();

    let timestamp = new Date(year, month - 1, dayLast);

    w = new Date(timestamp).getDay();//周几

    if (w === 0) {

      w = 7;

    }

    week = Math.ceil((timestamp.getDate() + 6 - w) / 7) - 1;

  }

  //week=cNum[week];

  let time = year + "年" + month + "月第" + week + "周";

  return time;

}
utils.getQuarterByDate = function (date) {
  date = new Date(date);
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  if (month < 4) {
    return `${year}年1季度`
  } else if (month < 7) {
    return `${year}年2季度`
  } else if (month < 10) {
    return `${year}年3季度`
  } else {
    return `${year}年4季度`
  }

}

utils.isNumber = function (value) {
  return typeof value === 'number' && !isNaN(value);
}

//把树结构得数据，拆分成普通的数组数据
utils.convert_tree_data = function (data) {
  data = JSON.parse(JSON.stringify(data))
  for (var i = 0; i < data.length; i++) {
    if (data[i].children != undefined) {
      var temp = data[i].children;
      // 删除孩子节点
      delete data[i].children;
      // 孩子节点加入数组末尾
      for (var j = 0; j < temp.length; j++) {
        data.push(temp[j]);
      }
    }
  }

  return data;
}


/**遍历数据成树 */

utils.getTrees = function (list, parentId, idKey, pidKey, parentNameKey) {
  let items = {};
  // 获取每个节点的直属子节点，*记住是直属，不是所有子节点
  for (let i = 0; i < list.length; i++) {
    let key = list[i][pidKey];
    if (items[key]) {
      items[key].push(list[i]);
    } else {
      items[key] = [];
      items[key].push(list[i]);
    }
  }
  return utils.formatTree(items, parentId, idKey, parentNameKey);
}

utils.formatTree = function (items, parentId, idKey, parentNameKey) {
  let result = [];
  if (!items[parentId]) {
    return result;
  }
  for (let t of items[parentId]) {
    let sub = utils.formatTree(items, t[idKey], idKey, parentNameKey);
    if (sub.length > 0) {
      sub.forEach(i => { i.parentName = t[parentNameKey] })
      t.children = sub;
    }
    result.push(t);
  }
  return result;
}


/**
 * 图片转成base64
 * @method
 * @param {String} imgURL
 * @return {String} dataURL
 */
utils.getBase64Image = function (url, callback) {
  var canvas = document.createElement("CANVAS");
  var ctx = canvas.getContext("2d");
  var img = new Image();
  img.crossOrigin = 'Anonymous';
  img.src = url;
  img.onload = function () {
    canvas.height = img.height;
    canvas.width = img.width;
    ctx.drawImage(img, 0, 0);
    var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
    // var dataURL = canvas.toDataURL();
    var dataURL = canvas.toDataURL("image/" + ext);
    var num = dataURL.indexOf(',');
    dataURL = dataURL.substring(num + 1);
    callback.call(this, dataURL);
    canvas = null;
  };
}


/**
 * 参数拼接
 * */
utils.jointParameter = function (data) {
  var str = "?";
  for (let i in data) {
    let j = data[i];
    if (j && (j + "").trim() !== "") {
      str += i + "=" + j + "&";
    }
  }
  str = str.substring(0, str.length - 1);
  return str;
}

/*
* 树重组
* */
utils.dealTreeData = function (data) {
  var parentMask = new Array();
  var root = new Array();

  root.push(data[0]);
  parentMask.push(data[0]);
  data.forEach(function (item, index) {
    if (index == 0) return;
    maskCompare(item);
  });

  function maskCompare(item) {
    if (item.parentMask > parentMask[parentMask.length - 1].parentMask) {//子节点
      if (!parentMask[parentMask.length - 1]._child)
        parentMask[parentMask.length - 1]._child = new Array();
      parentMask[parentMask.length - 1]._child.push(item);
      parentMask.push(item);
    } else if (item.parentMask <= parentMask[parentMask.length - 1].parentMask) {
      parentMask.pop();
      maskCompare(item);
    }
  }
  return root
}
//编码
utils.enpathParams = function (data) {
  return encodeURIComponent(JSON.stringify(data))
}
//解码
utils.depathParams = function (data) {
  return JSON.parse(decodeURIComponent(data))
}
//文件转base6
utils.fileFormatBase64 = function (file) {
  return new Promise((resolve, reject) => {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function (e) {
      if (e.target.result) {
        resolve(e.target.result)
      } else {
        reject('解析失败，请检查文件是否传入')
      }

    };
  })

}

//图片下载
utils.downloadIamge = function (imgsrc, name) {//下载图片地址和图片名
  var image = new Image();
  // 解决跨域 Canvas 污染问题
  image.setAttribute("crossOrigin", "anonymous");
  image.onload = function () {
    var canvas = document.createElement("canvas");
    canvas.width = image.width;
    canvas.height = image.height;
    var context = canvas.getContext("2d");
    context.drawImage(image, 0, 0, image.width, image.height);
    var url = canvas.toDataURL("image/png"); //得到图片的base64编码数据

    var a = document.createElement("a"); // 生成一个a元素
    var event = new MouseEvent("click"); // 创建一个单击事件
    a.download = name || "photo"; // 设置图片名称
    a.href = url; // 将生成的URL设置为a.href属性
    a.dispatchEvent(event); // 触发a的单击事件
  };
  image.src = imgsrc;
}
/**文件下载并改名 */
utils.downloadFile = function (typeName, id, name) {
  let url = `${process.env.VUE_APP_BASEURL}/AnnexAction/download?id=${encodeURI(
    id
  )}&typeName=${encodeURI(typeName)}`
  let a = document.createElement("a");
  document.body.appendChild(a);
  a.href = url;
  a.download = name; //命名下载名称
  a.click(); //点击触发下载  
  window.URL.revokeObjectURL(url); //下载完成进行释放
  document.body.removeChild(a);

}
/**附件信息转换成可以直接访问的URL */
utils.changeUrl = async ({ fileName = "附件", path = "" }) => {
  let res = await downloadFile({ fileName, path })
  const blob = new Blob([res]);
  const url = window.URL.createObjectURL(blob);
  return url
}
//传el-table的‘ref‘，table转excel
utils.tableToExcel = function (el, name) {
  let table = el.childNodes[1].childNodes[0]
  let body = el.childNodes[2].childNodes[0]
  if (false) {
    var idTmr;
    var uri = 'data:application/vnd.ms-excel;base64,',
      template = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--><meta charset="UTF-8"></head><body><table>{table}</table></body></html>',
      base64 = function (s) {
        return window.btoa(unescape(encodeURIComponent(s)))
      },
      format = function (s, c) {
        return s.replace(/{(\w+)}/g,
          function (m, p) {
            return c[p];
          })
      };
    if (!table.nodeType) table = document.getElementById(table);
    var ctx = { worksheet: name || 'Worksheet', table: table.innerHTML };
    window.location.href = uri + base64(format(template, ctx))

  }
  return table.innerHTML + body.childNodes[1].innerHTML
}
//后台返回二进制，进行blob，前提‘请求类型必须为blob’
utils.resultFileBlob = function (res, name = '数据导出报表', ext = '.xls') {
  let blob = new Blob([res])
  if (window.navigator && window.navigator.msSaveOrOpenBlob) {
    window.navigator.msSaveOrOpenBlob(blob, `${name}.${ext}`);
  } else {
    let blobUrl = window.URL.createObjectURL(blob);
    var a = document.createElement('a');
    a.download = `${name}.${ext}`;
    a.href = blobUrl;
    a.click();
  }
}
utils.fmDictData = (data) => {
  var obj = new Object();
  if (data) {
    data.map(i => {
      if (i.children) {
        obj[i.itemCode] = i.children;
        Object.assign(obj, utils.fmDictData(i.children))
      }
    })
  }
  return obj;
}
utils.fineDirtDataName = (dictData, code, value) => {
  if (!dictData) {
    return value
  }
  let data = code ? dictData[code] : []
  if (!data) {
    return value
  }
  let obj = data.find(i => i.itemCode == value)

  return obj ? obj["itemName"] : value
}
export default utils;
