import axios from 'axios'
import qs from 'qs'
import { Message, Loading } from 'element-ui';
import router from '../router'
import store from '../store'


export default function $axios(options) {
  return new Promise((resolve, reject) => {
    var loading = null
    let timeOut = 30000
    let timer = setTimeout(() => {
      loading = options.noLoading ? null : Loading.service({
        // target: '.loadDom',
        target: '.middleContainer',
        lock: true,
        background: 'rgba(255, 255, 255, 0.5)'
      })
    }, 600)
    //上传文件时，将等待时间加长为两分钟
    if (options.headers && options.headers["Content-Type"].indexOf("multipart/form-data") !== -1) {
      timeOut = 60000 * 10
    }

    const instance = axios.create({
      timeout: timeOut,
      withCredentials: true,
      baseURL: process.env.VUE_APP_BASEURL
    })
    //请求拦截器
    instance.interceptors.request.use(
      config => {
        config.headers.common['appCode'] = store.getters.systemCode
        let ticket = sessionStorage.getItem("ticket")
        //ie缓存问题
        // if (config.url.indexOf("?") != -1) {
        //   config.url = config.url + "&ticket=" + ticket + "&" + Math.floor(Math.random() * 100000)
        // } else {
        //   config.url = config.url + "?ticket=" + ticket + "&" + Math.floor(Math.random() * 100000)
        // }
        if (config.url.indexOf("?") != -1) {
          config.url = config.url + "&" + Math.floor(Math.random() * 100000)
        } else {
          config.url = config.url + "?" + Math.floor(Math.random() * 100000)
        }
        return config
      },
      error => {
        if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
          console.log('根据你设置的timeout/真的请求超时 判断请求现在超时了，你可以在这里加入超时的处理方案')
          // return service.request(originalRequest);//例如再重复请求一次
        }
        Message({
          showClose: true,
          message: `错误提示:${error}`,
          type: 'error'
        });
        return Promise.reject(error)
      }
    )
    // response 拦截器
    instance.interceptors.response.use(
      response => {
        if (!response.data.hasOwnProperty('code')) {
          return response.data;
        }
        switch (response.data.code) {
          case 0:
            return response.data;
          case 10000:
            sessionStorage.clear()
            router.push('/login')
          default:
            Message({
              showClose: true,
              message: `错误提示: ${response.data.description}`,
              type: 'error'
            });
            throw response.data.description;
        }
      },
      err => {
        if (err && err.response) {
          switch (err.response.status) {
            case 400:
              err.message = '请求错误'
              break
            case 401:
              err.message = '未授权，请登录'
              break
            case 403:
              err.message = '拒绝访问'
              break

            case 404:
              err.message = `请求地址出错: ${err.response.config.url}`
              break

            case 408:
              err.message = '请求超时'
              break

            case 500:
              err.message = '服务器内部错误'
              break

            case 501:
              err.message = '服务未实现'
              break

            case 502:
              err.message = '网关错误'
              break

            case 503:
              err.message = '服务不可用'
              break

            case 504:
              err.message = '网关超时'
              break

            case 505:
              err.message = 'HTTP版本不受支持'
              break

            default:
          }
        }
        Message({
          showClose: true,
          message: `错误提示: ${err.message.includes('timeout') ? '请求超时' : err}`,
          type: 'error'
        });
        return Promise.reject(err)
      }
    )
    //请求处理
    instance(options)
      .then((res) => {
        resolve(res)
        clearTimeout(timer)
        if (loading) {
          loading.close()
        }
        return false
      })
      .catch((error) => {
        reject(error)
        clearTimeout(timer)
        if (loading) {
          loading.close()
        }
      })
  })
}
