import {
    queryAssetTypeList,
    getDictData,
    querySupplierList,
    getDictDataTree,
    querySystemType
} from "@/api/common"
// import { getFlowList } from "@/api/flowManage/index";

//整理数据字典data  
const fmDictData = (data) => {
    var obj = new Object();
    if (data) {
        data.map(i => {
            if (i.children) {
                obj[i.regionCode] = i.children;
                Object.assign(obj, fmDictData(i.children))
            }
        })
    }
    return obj;
}
//整理资产分类下拉框 
const fmAssetType = (data) => {
    data.map(i =>
        (i.label = i.typeName, i.value = i.id, i.name = i.typeName)
    )
    return data;
}
//整理供应商 
const fmSupplier = (data) => {
    data.map(i =>
        (i.value = i.comFullName)
    )
    return data;
}

//应用系统名称下拉框
const fmSystemType = (data) => {
    data.map(i =>
        (i.label = i.sysName, i.value = i.id, i.name = i.sysName)
    )
    return data;
}

const common = {
    state: {
        dictData: {},//数据字典数据
        assetTypeList: null,//资产分类
        supplierList: null,//供应商信息
        systemType: null, //应用系统名称下拉框
        spareTypeList: null,//备件备品分类
        flowStatus: [//流程状态
            '停用',
            '启用',
            '草稿',
            '审核中',
            '驳回',
            '废弃',
            '转办',
        ],
        isPage: [//是否界面
            '界面',
            '接口'
        ],
        modelStatus: [//模块状态
            '禁用',
            '鉴权',
            '不鉴权'
        ]

    },
    mutations: {
        SET_DICTDATA: (state, dictData) => {
            state.dictData = fmDictData(dictData)
        },
        SET_ASSETTYPELIST: (state, assetTypeList) => {
            state.assetTypeList = fmAssetType(assetTypeList)
        },//
        SET_SPARETYPELIST: (state, spareTypeList) => {
            state.spareTypeList = fmAssetType(spareTypeList)
        },
        SET_SUPPLIERLIST: (state, supplierList) => {
            state.supplierList = fmSupplier(supplierList)
        },
        SET_SYSTEMTYPE: (state, systemType) => {
            state.systemType = fmSystemType(systemType)
        },
    },
    actions: {
        //==================================================================
        //获取资产分类下拉框信息
        QueryAssetTypeList({ commit }) {
            queryAssetTypeList().then(res => {
                let data = res.data || [];
                commit("SET_ASSETTYPELIST", data);
            }).catch(e => {
                console.log(e);
            })
        },

        //获取供应商的列表信息
        QuerySupplierList({ commit }) {
            querySupplierList().then(res => {
                let data = res.data.list || [];
                data.map(i => {
                    i.value = i.comFullName;
                    i.id = i.comNumber;
                    return i;
                });
                commit("SET_SUPPLIERLIST", data);
            }).catch(e => {
                console.log(e);
            })
        },

        //根据Code获取数据字典
        // GetDictData({ commit }, systemCode) {
        //     getDictData(systemCode).then(res => {
        //         let data = res.data || [];
        //         commit("SET_DICTDATA", data);
        //     }).catch(e => {
        //         console.log(e);
        //     })
        // },

        //获取数据字典树
        GetDictDataTree({ commit }) {
            return new Promise((resolve, reject) => {
                getDictDataTree().then(res => {
                    let data = res.data || [];
                    commit("SET_DICTDATA", data);
                    resolve(true)
                }).catch(e => {
                    reject(e)
                })
            })
        },

        //==================================================================

        GetDictData({ commit }) {
            return new Promise((resolve, reject) => {
                //获取字典、供应商信息、资产分类、应用系统名称下拉框
                Promise.all([getDictDataTree(), querySupplierList(), queryAssetTypeList("1"), querySystemType(), queryAssetTypeList("2"),]).then(results => {
                    let data = results[0].data || []
                    let supplierData = results[1].data.list || []
                    let assetTypeData = results[2].data || []
                    let systemType = results[3].data || []
                    let spareTypeList = results[4].data || []
                    commit("SET_DICTDATA", data)
                    commit("SET_SUPPLIERLIST", supplierData)
                    commit("SET_ASSETTYPELIST", assetTypeData)
                    commit("SET_SYSTEMTYPE", systemType)
                    commit("SET_SPARETYPELIST", spareTypeList)
                    resolve(true)
                }).catch(e => {
                    reject(e)
                })
            })
        },


        // GetFlowInfoCount({ commit }) {
        //     let form = { page: 1, pageSize: 5, type: 2 }
        //     getFlowList(form).then(res => {
        //         let count = res.data.total || 0
        //         commit("SET_HASFLOWCOUNT", count)
        //     }).catch(e => {
        //         console.log(e);
        //     })
        // },
        // //设置当前页面的按钮权限
        // SetScope({ commit }, scope) {
        //     return new Promise((resolve, reject) => {
        //         commit('SET_SCOPE', scope)
        //         resolve(true)
        //     })
        // },
    }
}
export default common