export default ([{
    path: '/itOperations',
    meta: {
        title: 'IT运维',
        type: "safem"
    },
    component: resolve => require(['@/views/itOperations'], resolve),
    children: [
        {
            path: '/',
            redirect: 'problemManagement'
        },
        {
            path: 'templatePage',
            meta: {
                title: '空白页面'
            },
            component: resolve => require(['@/components/templatePage'], resolve)
        },
        {
            path: 'problemManagement',
            meta: {
                title: '问题管理',
                type: "safem"
            },
            component: resolve => require([`@/views/itOperations/problemManagement`], resolve)
        },
        {
            path: 'changeManagement',
            meta: {
                title: '变更管理',
                type: "safem"
            },
            component: resolve => require([`@/views/itOperations/changeManagement`], resolve)
        }, 
        {
            path: 'releaseManagement',
            meta: {
                title: '发布管理',
                type: "safem"
            },
            component: resolve => require([`@/views/itOperations/releaseManagement`], resolve)
        },
        {
            path: 'spareStorage',
            meta: {
                title: '备件入库',
                type: "safem"
            },
            component: resolve => require([`@/views/itOperations/spareStorage`], resolve)
        }, 
        {
            path: 'spareRecipients',
            meta: {
                title: '备件领用',
                type: "safem"
            },
            component: resolve => require([`@/views/itOperations/spareRecipients`], resolve)
        },
        {
            path: 'applicationList',
            meta: {
                title: '应用系统清单',
                type: "safem"
            },
            component: resolve => require([`@/views/itOperations/applicationList`], resolve)
        },


    ]
}])