import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'




//请求
import api from './request/index'
Vue.use(api)
//常用方法
//utils
Vue.prototype.$utils = require('./utils/index').default;
//elementui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI)


//公司公共组件
import componentPublicCompany from 'component-public-company'
import 'component-public-company/src/lib/css/allStyle.css'
Vue.use(componentPublicCompany)

//项目公共组件
import components from './components/index'
Vue.use(components)

//各页面接口汇总
import InterfaceDetail from './InterfaceDetail/index'

//样式初始化
import './assets/css/reset.css'
//公共样式
import './assets/css/pubilcCss.css'

//iconfont图标
import './assets/iconfont/iconfont.css'
import '@/permission' // permission control



// Vue.use(api).use(utils).use(publicComponent).use(InterfaceDetail).use(componentPublicCompany).use(element)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
