<template>
  <div class="processScore">
    <div class="edit-table-toolbar">
      <div>{{ title }}</div>
      <div class="edit-btn" v-if="!hideBtn">
        <template v-for="(i, index) in btnConfig">
          <el-button
            class="btn-text-blue"
            size="mini"
            :key="index"
            v-if="showBtn(i.name)"
            :icon="i.icon"
            @click="i.methons"
            >{{ i.text }}</el-button
          >
        </template>
      </div>
    </div>
    <editForm
        :formConfig="formConfig"
        ref="scoreEditForm"
        :formData="editFormData"
      >
      </editForm>

  </div>
</template>

<script>
import { 
  addScore,// 评分记录-添加
  deleteScore,// 评分记录-删除
  scoreDetails,// 评分记录-详情
  updateScore// 评分记录-修改
 } from "@/api/processScore";
export default {
  props: {
    scoreSetting: {
      type: Object,
      default: () => {
        return { sourceId: "" };
      }
    },
    hideBtn: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      title:"",
      editFormData:{},
      btnArr: [],
      currentId:null,
      formConfig: {
        itemArr: [
          {
            label: "评分",
            prop: "score",
            type: "el-number",
            min: 0,
            max: 100,
            precision: 1,
            step: 1,
            // required: true,
            methods: (value) => {
              // this.score = value;
            },
          },
          {
            label: "申请描述",
            prop: "leamsg",
            type: "textarea",
            span: 24,
            rows: 4
          },
          // {
          //   label: "备注",
          //   prop: "remark",
          //   type: "textarea",
          //   span: 24,
          //   rows: 4
          // }
        ]
      },
      
      btnConfig: [
        {
          name: "save",
          text: "保存评分",
          icon: "el-icon-plus",
          methons: () => {
            this.save();
          }
        },
        {
          name: "delete",
          text: "删除评分",
          icon: "el-icon-delete",
          methons: () => {
            this.delete();
          }
        },
        // {
        //   name: "download",
        //   text: "下载",
        //   icon: "el-icon-download",
        //   methons: () => {
        //     this.download();
        //   }
        // },
        
        // {
        //   name: "save",
        //   text: "上传",
        //   icon: "el-icon-check",
        //   methons: () => {
        //     // this.save();
        //   }
        // }
      ],
    };
  },
    watch: {
    //监听附件
    "scoreSetting.sourceId"(val) {
      if (val) {
        this.getScore();
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      // console.log(this.scoreSetting);
      let { btnArr, title,billType,data, sourceId } = this.scoreSetting;
      this.btnArr = btnArr || [];
      this.title = title || "";
      this.editFormData = data || [];
      this.currentId = sourceId;
      if (sourceId) {
        this.getScore();
      }
    },
    async getScore() {
      // console.log("获取评分！");
      let res = await scoreDetails(this.currentId);
      // console.log(res);
      // this.editFormData = res.data[0] || {};
      // this.editFormData={
      //   score:"99",
      //   leamsg:"测试"
      // }
    },
    showBtn(btn) {
      return this.btnArr.indexOf(btn) != -1;
    },
        //获取表单数据
    getFormData() {
      let This = this;
      return new Promise((resolve, reject) => {
        this.$refs.scoreEditForm
          .getFormData()
          .then(data => {
            data = Object.assign({}, data);
            resolve(data);
          })
          .catch(e => {
            reject(e);
          });
      });
    },
    //保存评分
    save(){
       this.$refs.scoreEditForm
          .getFormData()
          .then(data => {
            data = Object.assign({}, data);
            data.id = this.currentId;
            // console.log(data);
            const actionApi = queryData => {
            //addRegist
            return queryData.id ? updateScore(queryData) : addScore(queryData);
          };
          actionApi(data).then(res => {
            this.$message.success("操作成功！");
            this.getScore();
          });
          })
      

    },
    //删除评分
    delete(){
      this.$confirm(
        "此操作将删除【此流程的评分】, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).then(() => {
          deleteScore(this.currentId)
            .then(res => {
              this.editFormData = {};
              this.$message.warning("删除成功！");
            })
            .catch(e => {
              this.$message.warning("删除失败！");
              console.log(e);
            });
        })
        .catch(e => {});

    },
  },
};
</script>

<style lang="scss" scoped>
.edit-table-toolbar{
  display: inline-block;
  float: right;
}
</style>