import request from '@/request/api'
// 评分记录-添加
export function addScore(data = {}) {
    return request({
        url: '/workorder/ScoremarkAction/addScoremark.htm',
        method: 'post',
        data:data
    })
}
// 评分记录-删除
export function deleteScore(id = "") {
    return request({
        url: '/workorder/ScoremarkAction/deleteScoremark.htm',
        method: 'post',
        data:{id:id}
    })
}

// 评分记录-详情
export function scoreDetails(id = "") {
    return request({
        url: '/workorder/ScoremarkAction/detail.htm',
        method: 'post',
        params:{sourceId:id}
    })
}

// 评分记录-修改
export function updateScore(data = {}) {
    return request({
        url: '/workorder/ScoremarkAction/updateScoremark.htm',
        method: 'post',
        data:data
    })
}

