<template>
  <div class="work-flow">
    <iframe
      class="work-flow__iframe"
      id="workflow"
      name="myFrame"
      frameborder="0"
      width="100%"
      ref="iframe"
      :src="iframeUrl"
    ></iframe>
    <dialogPublic
      :title="'信息选择'"
      :show.sync="showMember"
      @doSubmit="addSeleteMember"
      @doClose="()=>showMember=false"
      appendTobody
    >
      <orgMemberSelect ref="orgMemberSelect" v-if="showMember" :seleteSetting="orgMemberSetting"></orgMemberSelect>
    </dialogPublic>
  </div>
</template>
<script>
import { getFlowInfo, submitFlowInfo } from "@/api/workFlow/index";
import _ from "lodash"; //抖动函数，一定时间内多次点击只执行最后一次
export default {
  props: {
    //当前流程信息
    currentFlowData: {
      type: Object,
      default: function () {
        return {};
      },
    },
    //提交前回调函数
    beforeSubmit: {
      type: Object,
      default: function () {
        return {};
      },
    },
    //额外参数
    otherParams: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  data() {
    return {
      iframeUrl: "", //iframeUrl
      nodeKey: "", //标签节点key,用于操作流程上的node
      inputId: "", //标签节点id，用于操作流程上的node
      showMember: false,
      //人员选择时对应节点和人员对象
      checkNodeObj: {},
      //下个节点人员
      nextLeaderCode: {},
      procInstId: "", //流程id
      relatedUser: "", //转办人信息
      orgMemberSetting: {
        seleteType: "orgmember",
        radio: true,
        props: {
          label: "organizationName",
          children: "children",
        },
        column: [
          {
            label: "姓名",
            prop: "name",
            width: 60,
          },
        ],
        chosedData: [],
      },
    };
  },
  created() {
    this.initIframe();
  },
  mounted() {
    // this.getFlowInfo();
    window.addEventListener("message", this.workflowReceiveMessage);
  },
  beforeDestroy() {
    window.removeEventListener("message", this.workflowReceiveMessage);
  },
  methods: {
    //初始化iframe
    initIframe() {
      let { id } = this.currentFlowData;
      this.procInstId = id;
      let randomNum = Math.floor(Math.random() * 100000); //取随机数，防止IE缓存
      this.iframeUrl = `static/workflow/workflow.html?${randomNum}`;
    },
    // 监听回调
    workflowReceiveMessage(event) {
      // 根据上面制定的结构来解析iframe内部发回来的数据
      const data = event.data;
      switch (data.cmd) {
        case "height":
          this.setIframeHeight(data.params.height);
          break;
        /**点击提交流程时回调 */
        case "callBackSubmit":
          this.submitFlow(data.params.data);
          break;
        /**选择人员 */
        case "memberdialog":
          this.setIframemember(data.params.data);
          break;
        /**获取流程数据 */
        case "loadSure":
          this.getFlowInfo();
          break;
      }
    },
    //点击节点选择人员时
    setIframemember(data) {
      this.inputId = data;
      this.showMember = true;
    },
    //确定选择人员
    addSeleteMember() {
      let data = this.$refs.orgMemberSelect.getSeleteData();
      var wfHtml = document.getElementById("workflow").contentDocument;
      let input = wfHtml.getElementById(`${this.inputId}`);
      input.value = data
        .map((i) => {
          return i.name;
        })
        .join();

      input.dataset.code = data; //给input设置code属性
      this.relatedUser = data
        .map((i) => {
          return "1-" + i.workflowCode;
        })
        .join();

      this.showMember = false;
    },
    //获取流程图信息,并给子组件赋值html
    async getFlowInfo() {
      let res = await getFlowInfo({
        procInstId: this.procInstId,
        ...this.otherParams,
      });
      // console.log(res.data);
      this.$nextTick(() => {
        if (myFrame) {
          myFrame.window.setFlowHtml(res.data);
        }
      });
    },
    // 抖动函数，可再次处理业务数据，先提交确认信息
    toSubmintFlow: _.debounce((data) => {
      this.submitFlow(data);
    }, 200),
    // 提交工作流
    async submitFlow(data) {
      try {
        /**判断是否有回调函数需要处理业务数据 */
        if (this.beforeSubmit.hasOwnProperty("methods")) {
          await this.beforeSubmit.methods(data);
        }
        //如果有人员
        if (this.relatedUser) {
          data.subuser = this.relatedUser;
        }
        let { id } = this.currentFlowData;
        data.procInstId = id;
        data = Object.assign({}, data, this.otherParams);
        // console.log(data);
        submitFlowInfo(data).then((res) => {
          this.$message.success("流程提交成功！");
          let { procInstId } = res.data;
          this.procInstId = procInstId || this.procInstId;
          // //重新获取流程信息
          // this.getFlowInfo();
          // //获取待审数量,用于右上角显示
          // this.getFlowCount();
          this.$emit("submitSuccess", true);
        });
      } catch (error) {
        this.$message.error(error);
      }
    },
    //获取待审数量
    getFlowCount() {
      // this.$store.dispatch("GetFlowInfoCount");
    },
  },
};
</script>

<style >
.work-flow .work-flow__iframe {
  min-height: 400px;
  padding: 10px;
}
</style>