import Vue from 'vue'
import VueRouter from 'vue-router'
//解决重复点击一个导航
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

import itService from './modules/itService'
import itOperations from './modules/itOperations'
import itAssets from './modules/itAssets'
import itReport from './modules/itReport'
import systemManage from './modules/systemManage'
import flowManage from './modules/flowManage'

Vue.use(VueRouter)

const routes = [
    {
        path:'/',
        redirect:'/index',
    },
    {
        path: '/',
        component: () => import('@/views/index.vue'),
        children:[
            {
                path: '/index',
                name: '首页',
                component: (resolve) => require(['@/views/indexPage'], resolve)
            },
            ...itService,//IT服务
            ...itOperations,//IT运维
            ...itAssets,//IT资产
            ...itReport,//IT报表
            ...systemManage,//系统管理
            ...flowManage,//我的申请、审批
        ],
    },
]

const router = new VueRouter({
    routes
})

export default router
