export default ([{
    path: '/systemManage',
    meta: {
        title: '系统管理',
        type: "safem"
    },
    component: resolve => require(['@/views/systemManage'], resolve),
    children: [
        {
            path: 'templatePage',
            meta: {
                title: '空白页面'
            },
            component: resolve => require(['@/components/templatePage'], resolve)
        },
        {
            path: '/',
            redirect: 'departmentManage'
        },
        {
            path: 'departmentManage',
            meta: {
                title: '部门管理',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/departmentManage`], resolve)
        },
        {
            path: 'personManage',
            meta: {
                title: '人员管理',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/personManage`], resolve)
        },
        {
            path: 'powerManage',
            meta: {
                title: '权限管理',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/powerManage`], resolve)
        },
        {
            path: 'flowManage',
            meta: {
                title: '流程类型配置',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/flowManage`], resolve)
        },
        {
            path: 'slaManage',
            meta: {
                title: 'SLA配置',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/slaManage`], resolve)
        },
        {
            path: 'dictManage',
            meta: {
                title: '数据字典',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/dictManage`], resolve)
        },
        {
            path: 'codeManage',
            meta: {
                title: '编码规则',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/codeManage`], resolve)
        },
        {
            path: 'modelMenu',
            meta: {
                title: '模块菜单',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/modelMenu`], resolve)
        },
        {
            path: 'staffManage',
            meta: {
                title: '角色管理',
                type: "safem"
            },
            component: resolve => require([`@/views/systemManage/staffManage`], resolve)
        },
    ]
}])