/**附件信息 */
//附件列表查询
export function getAttachList(id) {
    return request({
        url: '/AnnexAction/queryList',
        method: 'post',
        data: { sourceId: id }
    })
}
//附件上传
export function uploadAttach(data, formData) {
    return request({
        url: `/AnnexAction/fileUpload?billType=${data.billType}&sourceId=${data.sourceId}`,
        method: 'post',
        headers: { "Content-Type": "multipart/form-data" },
        data: formData,
    })
}
